.activities{
    margin: 2rem 0px;
}
.activities .each-activity {
    text-align: center;
    padding: 0px 25px;
    margin: 1rem 0px;
}


.activities .each-activity .date {
    background-color: var(--main-color) !important;
    color: white;
    padding: 10px  ;
    text-align: center;
    margin: 0;
}
.activities .each-activity .read-more{
    background-color: rgba(243, 231, 231, 0.644);
}
.activities .each-activity button{
   border-radius: 50px;
   background-color: var(--main-color);
   color: white;
   padding: auto 20px !important;
   border-color: var(--main-color);
   margin: 10px 0px;
   text-transform: uppercase;
   font-weight: 600;
   font-size: 13px;

}
.activities .each-activity .detail{
    background-color: var(--secondary-color);
    padding: 20px 15px;
    text-align: left;
 
}
.activities .each-activity .detail h6 {
    font-weight: 700;
}
.activities .each-activity .detail small {
    color: var(--main-color);
    font-weight: 600;
}
.activity-banner h4{
    z-index: 2;
    text-transform: capitalize;
    font-weight: 600;

}
.activity-banner h3{
    z-index: 2;
    text-transform: uppercase;
    color: var(--main-color);
}
.activity-banner p{
    z-index: 2;
    padding: 10px 11rem;
    color: #0009;
}
.activity-banner .donate{
    z-index: 2;
    background-color: transparent;
    border-color: var(--main-color);
    border-radius: 50px;
    margin: 10px;
    text-transform: uppercase;
    color: var(--main-color);
}
.activity-banner .donate:hover, .activity-banner .donate:focus{
    background-color: var(--main-color);
    color: white;
    border-color: var(--main-color);
}
.activity-banner .read:hover, .activity-banner .read:focus{
    background-color: var(--main-color);
    color: white;
    border-color: var(--main-color);
}
.activity-banner .read{
    z-index: 2;
    background-color: var(--main-color);
    border-color: var(--main-color);
    border-radius: 50px;
    margin: 10px;
    text-transform: uppercase;
}
.activity-banner h4::after{
    content: '';
    background-color: var(--main-color) ;
    width: 30%;
    height: 3px;
    border-radius: 50px;
    margin-top: 20px !important;
    margin-bottom: 20px !important;
    display: block;
    text-align: center;
    margin: auto;
}
.activity-banner{
    background-image: url('../assets//activitybg.jpg') ;
    background-position: center center;
    background-size: cover;
    justify-content: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 400px;
    text-align: center;
    position: relative;
    margin-top: 2rem;
}
.activity-banner::before{
    background: rgba(255, 255, 255, 0.801);
    content: "";
    top: 0;
    width: 100%;
    height: 400px;
    z-index: 0;
    position: absolute;
}

.activity-details{
    margin: 20px 30px;
}
.activity-details .image{
    background-color: white;
    padding: 50px;
}
.activity-details .image .location{
    background-color: var(--main-color);
    padding:14px;
    font-size: 16px;
}
.activity-details .image .location svg{
    margin-bottom: 5px;
    margin-left: 10px;
}

.activity-details  .date{
    background-color: var(--secondary-color) !important;
    padding:14px;
    font-size: 16px;
}
.activity-details .image .date svg{
    margin-bottom: 5px;
    margin-left: 10px;
    margin-right: 5px;
}
.activity-details .content{
    margin-top: 40px;
}
.activity-details h5{
   margin: 10px 0px;
}
.activity-details .content p{
    font-size: 16px;
    line-height: 2.0rem;
    letter-spacing: 0.2px;
    color: #0009;
}
.activity-details .content .details-section{
    padding: 20px;
    color: white;
}
.activity-details .content .details-section svg{
    margin-top: 10px;
}
.activity-details .content .details-section p{
    margin: 0;
    padding: 0;
    color: white;
}
.activity-details .content .details-section .time{
    background-color: var(--secondary-color);
    padding: 20px;
    margin-right:0px
}
.activity-details .content .details-section .date2{
    background-color: orange !important;
    padding: 20px;
    margin-right:0px

}
.activity-details .content .details-section .address{
    background-color: var(--main-color);
    padding: 20px;
    margin-right:0px

}
.activity-gallery{
    position: relative;
}
.activity-gallery h6{
    text-transform: uppercase;
}
.activity-gallery .activity-gallery-icons{
    position: absolute;
    right: 9%;
    top: 5%;
}
.activity-gallery .activity-gallery-icons span{
   padding: 10px;
   color: var(--main-color);
   cursor: pointer;
}
.activity-slider {
    margin: 4rem 0px;
}
.activity-slider h6{
    text-transform: uppercase;
    font-weight: 700;
    color: var(--secondary-color);
}
.activity-slider h1{
    font-weight: 700;
    color: var(--main-color);
}
.activity-slider .slick-prev:before{
    color: var(--main-color) !important;
    font-size: 25px;

}
 .activity-slider .slick-next:before{
    color: var(--main-color) !important;
    font-size: 25px;
}
.activity-slider .each-activity{
    padding: 10px;
    margin: 30px 0px;
    position: relative;
}
.activity-slider .each-activity img{
    border-radius: 10px 70px 0px 0px;
}
.activity-slider .each-activity .content{
    border: 1px solid rgba(0, 0, 0, 0.116);
    padding: 20px;
    border-radius: 0px 0px 10px 10px;

}
.activity-slider .each-activity .content h5{
    font-weight: 700;
}
.activity-slider .each-activity .content .userby{
    margin: 10px 0px;
    color: var(--main-color);
}
.activity-slider .each-activity .content .userby svg{
    margin-left: 10px;
    margin-bottom: 5px;
}
.activity-slider .each-activity .content p{
    color: #0009;
}
.activity-slider .each-activity .content button{
    border-radius: 50px;
    background-color: var(--main-color);
    border-color: var(--main-color);
    font-size: 15px;
}
.activity-slider .each-activity .content b{
    color: var(--main-color);

}
.activity-slider .each-activity .activity-gallery-icons{
    position: absolute;
    top: 20px;
    left: 20px;
}
.activity-slider .each-activity .activity-gallery-icons svg{
   color: var(--main-color) !important;
   cursor: pointer;
}